* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.App {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    padding: 20px;
    overflow: hidden;
    transition: all 0.2s;
}
.box {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    padding: 20px;
    background-color: #fff;
    border-radius: 20px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    overflow: hidden;
}
.box:first-child {
    margin-right: 20px;
}
.box_left .img_container {
    display: flex;
    flex-wrap: wrap;
}
.box_left .img_container .image {
    width: 50%;
    height: fit-content;
}
.box_right > .list .item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    padding: 10px 20px;
    background-color: #fff;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.1s;
}
.box_right > .list .item:hover {
    color: #fff;
    background-color: black;
    box-shadow: 0 0 10px 0 rgba(255, 255, 255, 1) inset;
}
.box_right .btn_box {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.box_right .btn_box .btn {
    padding: 8px 20px;
    color: #fff;
    background-color: skyblue;
    border-radius: 10px;
    cursor: pointer;
}
.box_right .btn_box .btn:hover {
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);
}
.kws {
    padding: 10px 20px;
    background-color: #fff;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    border-radius: 10px;
}
.kws .lora {
    font-weight: bold;
    margin-bottom: 10px;
}
.kws .list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 0;
}
.kws .list .item {
    margin-right: 10px;
    margin-bottom: 10px;
    padding: 5px 10px;
    background-color: #fff;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    cursor: pointer;
}
.prompt {
    width: 100%;
    padding: 10px 20px;
    font-size: 16px;
    font-family: inherit;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    border: none;
    outline: none;
    resize: none;
}
@media screen and (max-width: 500px) {
    .App {
        flex-direction: column;
    }
    .box_left {
        flex: 6;
    }
    .box_right {
        flex: 4;
    }
    .box:first-child {
        margin-right: 0;
        margin-bottom: 20px;
    }
}
